@font-face {
    font-family: Montserrat;
    src: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) format('woff2');
}

@font-face {
    font-family: Fredoka;
    src: url(${___CSS_LOADER_URL_REPLACEMENT_1___}) format('woff2');
}

/*
Josh's Custom CSS Reset
https://www.joshwcomeau.com/css/custom-css-reset/
*/
*, *::before, *::after {
    box-sizing: border-box;
}
* {
    margin: 0;
}
body {
    line-height: 1.5;
    -webkit-font-smoothing: antialiased;
}
img, picture, video, canvas, svg {
    display: inline-block;
    max-width: 100%;
}
input, button, textarea, select {
    font: inherit;
}
p, h1, h2, h3, h4, h5, h6 {
    overflow-wrap: break-word;
    hyphens: auto;
}
p {
    text-wrap: pretty;
}
h1, h2, h3, h4, h5, h6 {
    text-wrap: balance;
}
button {
    line-height: 1;
}
button:hover {
    cursor: pointer;
}

/*Variables*/
:root {
    --header-dialog-bg-color: #cdc1ff;
    --generic-shadow: 0px 4px 4px gray;
    --task-nav-bg-color: #A294F9;
    --button-color: #8379C3;
    --nav-toggle-size: 48px;
    --action-button-size: 36px;
    --small-button-size: 20px;
    --button-shadow: 0px 2px 4px #554f7e;
    --main-bg-color: #F5EFFF;
}

/*General*/
body {
    height: 100vh;
    display: grid;
    grid-template-areas: 
        "header header"
        "nav main";
    grid-template-rows: 120px 1fr; 
    grid-template-columns: 360px 1fr;
    transition: 0.3s;
    background-color: var(--main-bg-color);
}

span {
    font-family: Montserrat, sans-serif;
}

h1, h2, h3 {
    font-family: Fredoka, sans-serif;
}

h1 {
    font-size: 4rem;
}

h2 {
    font-size: 2rem;
}

h3 {
    font-size: 1.5rem;
}

.button-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 8px;
}

.small-button {
    height: var(--small-button-size);
    width: var(--small-button-size);
    background-color: inherit;
    color: inherit;
    background-size: contain;
    background-repeat: no-repeat;
    border: none;
    transition: 0.3s;
    border-radius: 4px;
}

.action-button {
    transition: 0.3s;
    height: var(--action-button-size); 
    width: var(--action-button-size); 
    border-radius: 8px;
    border: none;
    font-family: Montserrat, sans-serif;
    font-size: 1.5rem;
    background-color: var(--button-color); 
    color: white;
    box-shadow: var(--button-shadow); 
}

.action-button:hover {
    transform: scale(1.1);
    filter: brightness(90%);
}

.action-button:active {
    transition: 0.1s;
    transform: scale(0.9);
}

/*Header*/
header {
    grid-area: header;
    background-color: var(--header-dialog-bg-color); 
    display: grid;
    place-content: center;
    box-shadow: var(--generic-shadow);
    text-shadow: var(--generic-shadow); 
    z-index: 1; /* To shadow all elements*/
    position: relative;
}

#nav-toggle {
    height: var(--nav-toggle-size);
    width: var(--nav-toggle-size);
    position: absolute;
    top: 50%;
    left:0;
    transform: translateY(-50%);
    margin: 0px 36px;
    border-radius: 8px;
    border: none;
    background-color: var(--button-color); 
    box-shadow: var(--button-shadow); /*fix*/
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
    background-size: cover;
    transition: 0.3s;
}

#nav-toggle:hover {
    transform: translateY(-50%) scale(1.1);
}

#nav-toggle:active {
    transform: translateY(-50%) scale(0.8);
}

/*Nav*/
nav {
    transition: 0.3s;
    grid-area: nav;
    background-color: var(--task-nav-bg-color); 
    color: white;
    padding: 48px;
    transform: translateX(-20px);
}

.h2-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

nav h2 {
    text-shadow: var(--generic-shadow); 
}

.nav-button {
    display: block;
    height: 36px;
    width: 100%;
    border: 0;
    border-radius: 8px;
    text-align: left;
    padding: 0px 24px;
    font-family: Montserrat, sans-serif;
    background-color: inherit;
    color: white;
    font-size: 1.25rem;
    transition: 0.3s;
    text-shadow: var(--generic-shadow); 
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.nav-button:hover {
    transform: scale(1.05);
    background-color: var(--button-color);
}

.nav-button:active {
    transform: scale(0.9);
}

nav .button-wrapper {
    margin: 8px 0px;
}

nav .selected {
    background-color: var(--button-color);
    border-radius: 8px;
}

nav .removed {
    transform: translateX(-100vw);
}

/*Main*/
main {
    background-color: var(--main-bg-color);
    padding: 48px;
    min-width: 480px;
}

#filter-button {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_3___});
    background-size: 50%;
    background-repeat: no-repeat;
    background-position: center;
}

.task {
    height: 36px;
    width: 100%;
    border-radius: 8px;
    margin-bottom: 8px;
    font-family: Montserrat, sans-serif;
    background-color:var(--task-nav-bg-color); 
    color: white;
    box-shadow: var(--generic-shadow); 
    padding: 0 16px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 8px;
    opacity: 0;
    transition: 0.3s;
}

.task.displayed {
    opacity: 1;
}

.task.removed {
    opacity: 0;
}

.task-checkbox {
    height: var(--small-button-size);
    width: var(--small-button-size);
    border-radius: 8px;
    border: none;
    transition: 0.3s;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_4___});
    background-size: 0%;
    background-position: center;
    background-repeat: no-repeat;
}

.task-checkbox:hover,
.small-button:hover {
    filter: brightness(90%);
}

.task-checkbox.checked {
    background-size: 80%;
}

.task-name {
    flex: 1;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    /*Name cross-out has transition from transparent to white after 0.3s*/
    text-decoration-color: transparent;
    transition: 0.3s;
}

/*Cross out name of completed tasks*/
.task:has(.task-checkbox.checked) .task-name {
    text-decoration-color: white;
    text-decoration: line-through;
}

.task-info {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_5___});
}

.star {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_6___});
}

.star.checked {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_7___});
}

.edit {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_8___});
}

.delete {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_9___});
}

/*Hide inner checkbox of checkbox type buttons*/
.checkbox > input[type="checkbox"] {
    display: none;
}

/*Dialogs*/
dialog { 
    position: absolute;
    top: 50%;
    left: 50%;
    width: 480px;
    height: fit-content;
    transform: translate(-50%, -50%) scale(0);
    border: none;
    border-radius: 8px;
    box-shadow: var(--generic-shadow);
    padding: 32px;
    background-color: var(--header-dialog-bg-color);
}

dialog.open {
    transform: translate(-50%, -50%) scale(1);
}

.close,
.confirm {
    background-size: 50%;
    background-repeat: no-repeat;
    background-position: center;
}

.close {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_10___});
}

.confirm {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_11___});
}

label { 
    font-family: Montserrat, sans-serif;
    font-weight: bold;
}

input,
textarea { 
    width: 100%;
    border-radius: 8px;
    border: none;
    resize: none;
    margin-bottom: 8px;
}

.validation {
    display: none;
}

.validation.invalid {
    display: block;
    font-weight: normal;
    color: red;
}

input.invalid {
    box-shadow: 0px 0px 4px 0px red;
}

dialog .button-wrapper {
    margin-top: 16px;
}

input,
textarea,
.task-info-content {
    font-weight: normal;
}


#task-info-dialog label {
    display: inline-block;
    margin: 8px 0;
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA,oBAAoB;AACpB;IACI,uBAAuB;IACvB,4DAAyD;AAC7D;;AAEA;IACI,oBAAoB;IACpB,4DAAsD;AAC1D;;AAEA;;;CAGC;AACD;IACI,sBAAsB;AAC1B;AACA;IACI,SAAS;AACb;AACA;IACI,gBAAgB;IAChB,mCAAmC;AACvC;AACA;IACI,qBAAqB;IACrB,eAAe;AACnB;AACA;IACI,aAAa;AACjB;AACA;IACI,yBAAyB;IACzB,aAAa;AACjB;AACA;IACI,iBAAiB;AACrB;AACA;IACI,kBAAkB;AACtB;AACA;IACI,cAAc;AAClB;AACA;IACI,eAAe;AACnB;;AAEA,YAAY;AACZ;IACI,iCAAiC;IACjC,kCAAkC;IAClC,4BAA4B;IAC5B,uBAAuB;IACvB,uBAAuB;IACvB,0BAA0B;IAC1B,yBAAyB;IACzB,oCAAoC;IACpC,wBAAwB;AAC5B;;AAEA,UAAU;AACV;IACI,aAAa;IACb,aAAa;IACb;;kBAEc;IACd,6BAA6B;IAC7B,gCAAgC;IAChC,gBAAgB;IAChB,sCAAsC;AAC1C;;AAEA;IACI,mCAAmC;AACvC;;AAEA;IACI,gCAAgC;AACpC;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,iBAAiB;AACrB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,QAAQ;AACZ;;AAEA;IACI,gCAAgC;IAChC,+BAA+B;IAC/B,yBAAyB;IACzB,cAAc;IACd,wBAAwB;IACxB,4BAA4B;IAC5B,YAAY;IACZ,gBAAgB;IAChB,kBAAkB;AACtB;;AAEA;IACI,gBAAgB;IAChB,iCAAiC;IACjC,gCAAgC;IAChC,kBAAkB;IAClB,YAAY;IACZ,mCAAmC;IACnC,iBAAiB;IACjB,qCAAqC;IACrC,YAAY;IACZ,gCAAgC;AACpC;;AAEA;IACI,qBAAqB;IACrB,uBAAuB;AAC3B;;AAEA;IACI,gBAAgB;IAChB,qBAAqB;AACzB;;AAEA,SAAS;AACT;IACI,iBAAiB;IACjB,+CAA+C;IAC/C,aAAa;IACb,qBAAqB;IACrB,iCAAiC;IACjC,kCAAkC;IAClC,UAAU,EAAE,0BAA0B;IACtC,kBAAkB;AACtB;;AAEA;IACI,8BAA8B;IAC9B,6BAA6B;IAC7B,kBAAkB;IAClB,QAAQ;IACR,MAAM;IACN,2BAA2B;IAC3B,gBAAgB;IAChB,kBAAkB;IAClB,YAAY;IACZ,qCAAqC;IACrC,gCAAgC,EAAE,MAAM;IACxC,yDAA4D;IAC5D,sBAAsB;IACtB,gBAAgB;AACpB;;AAEA;IACI,sCAAsC;AAC1C;;AAEA;IACI,sCAAsC;AAC1C;;AAEA,MAAM;AACN;IACI,gBAAgB;IAChB,cAAc;IACd,0CAA0C;IAC1C,YAAY;IACZ,aAAa;IACb,4BAA4B;AAChC;;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;AACvB;;AAEA;IACI,kCAAkC;AACtC;;AAEA;IACI,cAAc;IACd,YAAY;IACZ,WAAW;IACX,SAAS;IACT,kBAAkB;IAClB,gBAAgB;IAChB,iBAAiB;IACjB,mCAAmC;IACnC,yBAAyB;IACzB,YAAY;IACZ,kBAAkB;IAClB,gBAAgB;IAChB,kCAAkC;IAClC,mBAAmB;IACnB,gBAAgB;IAChB,uBAAuB;AAC3B;;AAEA;IACI,sBAAsB;IACtB,qCAAqC;AACzC;;AAEA;IACI,qBAAqB;AACzB;;AAEA;IACI,eAAe