ay: inline-block;\n    max-width: 100%;\n}\ninput, button, textarea, select {\n    font: inherit;\n}\np, h1, h2, h3, h4, h5, h6 {\n    overflow-wrap: break-word;\n    hyphens: auto;\n}\np {\n    text-wrap: pretty;\n}\nh1, h2, h3, h4, h5, h6 {\n    text-wrap: balance;\n}\nbutton {\n    line-height: 1;\n}\nbutton:hover {\n    cursor: pointer;\n}\n\n/*Variables*/\n:root {\n    --header-dialog-bg-color: #cdc1ff;\n    --generic-shadow: 0px 4px 4px gray;\n    --task-nav-bg-color: #A294F9;\n    --button-color: #8379C3;\n    --nav-toggle-size: 48px;\n    --action-button-size: 36px;\n    --small-button-size: 20px;\n    --button-shadow: 0px 2px 4px #554f7e;\n    --main-bg-color: #F5EFFF;\n}\n\n/*General*/\nbody {\n    height: 100vh;\n    display: grid;\n    grid-template-areas: \n        \"header header\"\n        \"nav main\";\n    grid-template-rows: 120px 1fr; \n    grid-template-columns: 360px 1fr;\n    transition: 0.3s;\n    background-color: var(--main-bg-color);\n}\n\nspan {\n    font-family: Montserrat, sans-serif;\n}\n\nh1, h2, h3 {\n    font-family: Fredoka, sans-serif;\n}\n\nh1 {\n    font-size: 4rem;\n}\n\nh2 {\n    font-size: 2rem;\n}\n\nh3 {\n    font-size: 1.5rem;\n}\n\n.button-wrapper {\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    gap: 8px;\n}\n\n.small-button {\n    height: var(--small-button-size);\n    width: var(--small-button-size);\n    background-color: inherit;\n    color: inherit;\n    background-size: contain;\n    background-repeat: no-repeat;\n    border: none;\n    transition: 0.3s;\n    border-radius: 4px;\n}\n\n.action-button {\n    transition: 0.3s;\n    height: var(--action-button-size); \n    width: var(--action-button-size); \n    border-radius: 8px;\n    border: none;\n    font-family: Montserrat, sans-serif;\n    font-size: 1.5rem;\n    background-color: var(--button-color); \n    color: white;\n    box-shadow: var(--button-shadow); \n}\n\n.action-button:hover {\n    transform: scale(1.1);\n    filter: brightness(90%);\n}\n\n.action-button:active {\n    transition: 0.1s;\n    transform: scale(0.9);\n}\n\n/*Header*/\nheader {\n    grid-area: header;\n    background-color: var(--header-dialog-bg-color); \n    display: grid;\n    place-content: center;\n    box-shadow: var(--generic-shadow);\n    text-shadow: var(--generic-shadow); \n    z-index: 1; /* To shadow all elements*/\n    position: relative;\n}\n\n#nav-toggle {\n    height: var(--nav-toggle-size);\n    width: var(--nav-toggle-size);\n    position: absolute;\n    top: 50%;\n    left:0;\n    transform: translateY(-50%);\n    margin: 0px 36px;\n    border-radius: 8px;\n    border: none;\n    background-color: var(--button-color); \n    box-shadow: var(--button-shadow); /*fix*/\n    background-image: url(\"./assets/images/nav-toggle-icon.svg\");\n    background-size: cover;\n    transition: 0.3s;\n}\n\n#nav-toggle:hover {\n    transform: translateY(-50%) scale(1.1);\n}\n\n#nav-toggle:active {\n    transform: translateY(-50%) scale(0.8);\n}\n\n/*Nav*/\nnav {\n    transition: 0.3s;\n    grid-area: nav;\n    background-color: var(--task-nav-bg-color); \n    color: white;\n    padding: 48px;\n    transform: translateX(-20px);\n}\n\n.h2-wrapper {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n}\n\nnav h2 {\n    text-shadow: var(--generic-shadow); \n}\n\n.nav-button {\n    display: block;\n    height: 36px;\n    width: 100%;\n    border: 0;\n    border-radius: 8px;\n    text-align: left;\n    padding: 0px 24px;\n    font-family: Montserrat, sans-serif;\n    background-color: inherit;\n    color: white;\n    font-size: 1.25rem;\n    transition: 0.3s;\n    text-shadow: var(--generic-shadow); \n    white-space: nowrap;\n    overflow: hidden;\n    text-overflow: ellipsis;\n}\n\n.nav-button:hover {\n    transform: scale(1.05);\n    background-color: var(--button-color);\n}\n\n.nav-button:active {\n    transform: scale(0.9);\n}\n\nnav .button-wrapper {\n    margin: 8px 0px;\n}\n\nnav .selected {\n    background-color: var(--button-color);\n    border-radius: 8px;\n}\n\nnav .removed {\n    transform: translateX(-100vw);\n}\n\n/*Main*/\nmain {\n    background-color: var(--main-bg-color);\n    padding: 48px;\n    min-width: 480px;\n}\n\n#filter-button {\n    background-image: url(\"./assets/images/filter.svg\");\n    background-size: 50%;\n    background-repeat: no-repeat;\n    background-position: center;\n}\n\n.task {\n    height: 36px;\n    width: 100%;\n    border-radius: 8px;\n    margin-bottom: 8px;\n    font-family: Montserrat, sans-serif;\n    background-color:var(--task-nav-bg-color); \n    color: white;\n    box-shadow: var(--generic-shadow); \n    padding: 0 16px;\n    display: flex;\n    align-items: center;\n    justify-content: space-between;\n    gap: 8px;\n    opacity: 0;\n    transition: 0.3s;\n}\n\n.task.displayed {\n    opacity: 1;\n}\n\n.task.removed {\n    opacity: 0;\n}\n\n.task-checkbox {\n    height: var(--small-button-size);\n    width: var(--small-button-size);\n    border-radius: 8px;\n    border: none;\n    transition: 0.3s;\n    background-image: url(\"./assets/images/check.svg\");\n    background-size: 0%;\n    background-position: center;\n    background-repeat: no-repeat;\n}\n\n.task-checkbox:hover,\n.small-button:hover {\n    filter: brightness(90%);\n}\n\n.task-checkbox.checked {\n    background-size: 80%;\n}\n\n.task-name {\n    flex: 1;\n    white-space: nowrap;\n    overflow: hidden;\n    text-overflow: ellipsis;\n    /*Name cross-out has transition from transparent to white after 0.3s*/\n    text-decoration-color: transparent;\n    transition: 0.3s;\n}\n\n/*Cross out name of completed tasks*/\n.task:has(.task-checkbox.checked) .task-name {\n    text-decoration-color: white;\n    text-decoration: line-through;\n}\n\n.task-info {\n    background-image: url(\"./assets/images/info.svg\");\n}\n\n.star {\n    background-image: url(\"./assets/images/star-unclicked.svg\");\n}\n\n.star.checked {\n    background-image: url(\"./assets/images/star-clicked.svg\");\n}\n\n.edit {\n    background-image: url(\"./assets/images/edit.svg\");\n}\n\n.delete {\n    background-image: url(\"./assets/images/delete.svg\");\n}\n\n/*Hide inner checkbox of checkbox type buttons*/\n.checkbox > input[type=\"checkbox\"] {\n    display: none;\n}\n\n/*Dialogs*/\ndialog { \n    position: absolute;\n    top: 50%;\n    left: 50%;\n    width: 480px;\n    height: fit-content;\n    transform: translate(-50%, -50%) scale(0);\n    border: none;\n    border-radius: 8px;\n    box-shadow: var(--generic-shadow);\n    padding: 32px;\n    background-color: var(--header-dialog-bg-color);\n}\n\ndialog.open {\n    transform: translate(-50%, -50%) scale(1);\n}\n\n.close,\n.confirm {\n    background-size: 50%;\n    background-repeat: no-repeat;\n    background-position: center;\n}\n\n.close {\n    background-image: url(\"./assets/images/close.svg\");\n}\n\n.confirm {\n    background-image: url(\"./assets/images/confirm.svg\");\n}\n\nlabel { \n    font-family: Montserrat, sans-serif;\n    font-weight: bold;\n}\n\ninput,\ntextarea { \n    width: 100%;\n    border-radius: 8px;\n    border: none;\n    resize: none;\n    margin-bottom: 8px;\n}\n\n.validation {\n    display: none;\n}\n\n.validation.invalid {\n    display: block;\n    font-weight: normal;\n    color: red;\n}\n\ninput.invalid {\n    box-shadow: 0px 0px 4px 0px red;\n}\n\ndialog .button-wrapper {\n    margin-top: 16px;\n}\n\ninput,\ntextarea,\n.task-info-content {\n    font-weight: normal;\n}\n\n\n#task-info-dialog label {\n    display: inline-block;\n    margin: 8px 0;\n}"],"sourceRoot":""}]);
